<template>
  <div class="guides-molecule">
    <div v-if="!selectedGuide" class="guides-molecule__message">
      <p>No guide is available for the selected item or block </p>
    </div>
    <div v-if="selectedGuide" class="guides-molecule__container lc-custom-scroll">
      <table>
        <tr v-for="guide of selectedGuide" :key="guide.key">
          <td class="title">{{guide.caption}}</td>
          <td class="value">{{guide.value}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import GuidesMolecule from './GuidesMolecule.hook'
const hook = new GuidesMolecule();
const tableKeyMapping = {
  caption: "Block Type",
  description:"Description",
  blockCategory:"Block Category",
  inputVariable:"Input Variable Type",
  inputType:"Input Type",
  returnType:"Return Type"
};

export default {
  name: 'GuidesMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("sampleMethod"),
  computed: {
    selectedGuide(){
      if (this.selectedBlock && this.guides){
        const guideObj = this.guides[this.selectedBlock.type];
        if (guideObj){
          const tableData = [];
          Object.keys(tableKeyMapping).forEach(key=>{
            const value = guideObj.info[key];
            if (value){
              tableData.push({
                key,
                caption: tableKeyMapping[key],
                value
              })
            }

          });
          return tableData;
        }
      }
      return null;
    },
    ...hook.getters("guides","selectedBlock")
  },
  props: { },
  data() {
      return { }
  }
}

</script>

<style scoped lang="scss">

.guides-molecule {
  height: calc(100vh - 100px);

  &__message {
    padding:20px;
    font-size:18px;
    opacity: 0.5;
  }

  &__container { 
    height:calc(100% - 20px);
    overflow-y: scroll;
    padding-left:10px;
    padding-right:10px;

    table {
      tr {
        font-size:14px;
        td{
          vertical-align: top;
          padding-bottom: 10px;
        }

        td.title{
          width:100px;
          // color: rgb(185, 185, 185);
        }

        td.value{
          // color: rgb(196, 196, 196);
        }

      }
    }
  }
}

</style>
